.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #fff; /* background-color: #f8f9fa; */ 
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  cursor: none; /* hide default cursor */
}

.logo {
  max-width: 300px;
  height: auto;
  margin-bottom: 2rem;
}

.join-button {
  padding: 0.8rem 1.5rem;
  background-color: #0a2540;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  font-size: 1.1rem;
  font-weight: bold;
  transition: background-color 0.3s ease;
  cursor: none !important;
}

.join-button:hover {
  background-color: #133a5e;
}

/* Hide the native cursor everywhere inside the app (incl. links/buttons) */
.App * {
  cursor: none !important;
}

/* Smooth size change for the custom cursor */
.circle-cursor {
  position: fixed;
  width: 12px;
  height: 12px;
  background-color: rgb(135, 206, 235);
  border-radius: 50%;
  pointer-events: none;
  z-index: 9999;
  display: none;
  transform: translate(-50%, -50%);
  transition: width 0.15s ease, height 0.15s ease;
}
